/* --------------
* article瀑布式
* -------------- */
article.articles{
    -webkit-column-width: 23em;
            column-width: 23em;
    -webkit-column-gap: 1em;
            column-gap: 1em;
}

@media (max-width: 1440px){
    article.articles{
        -webkit-column-width: 21em;
                column-width: 21em;
        -webkit-column-gap: 1em;
                column-gap: 1em;
    }
}

@media (max-width: 1024px){
    article.articles{
        -webkit-column-width: 20em;
                column-width: 20em;
        -webkit-column-gap: 1em;
                column-gap: 1em;
    }
}

@media (max-width: 768px){
    article.articles{
        -webkit-column-width: 18em;
                column-width: 18em;
        -webkit-column-gap: 8px;
                column-gap: 8px;
    }
}

/* ------------------
* 包住postCover的a標籤
* ------------------- */
.articles a {
    display: block;
}

/* --------------
* .Lazyload
* --------------- */
.LazyLoad{
    display: inline-block;
    width: 100%;
    /* height: 512px; */
}
.LazyLoad.is-visible{
    height: auto;
}



